<script setup lang="ts">
import { DEVICE } from "~/constants";

export interface CardDeviceProps {
  title?: string;
  date?: string;
  disabled?: boolean;
  type?: TDeviceType;
  active?: boolean;
}
const props = defineProps<CardDeviceProps>();
const emit = defineEmits(["delete"]);
const { t } = useI18n();

function onDeleteClick() {
  emit("delete");
}
</script>

<template>
  <div class="cardDevice" :class="{ cardDevice__disabled: props.disabled }">
    <div v-if="props.type" class="cardDevice__type dark:bg-[var(--base-card-bgSecondaryColor)] dark:border-none dark:border-[#d9d9d9] border-[0.5px]">
      <IconPhone v-if="props.type === DEVICE.MOBILE" fill="var(--base-icon-color)" />
      <IconTablet v-else-if="props.type === DEVICE.TABLET" fill="var(--base-icon-color)" />
      <IconDesktop v-else-if="props.type === DEVICE.DESKTOP" fill="var(--base-icon-color)" />
    </div>
    <div class="flex flex-col justify-between flex-1">
      <div class="flex justify-between">
        <h3 class="cardDevice__title">
          {{ props.title }}
        </h3>
        <BaseButton type="icon" name="delete-device" @click="onDeleteClick()">
          <IconDelete class="cardDevice__delete" fill="var(--base-error-color)" />
        </BaseButton>
      </div>
      <div v-if="props.date" class="cardDevice__date">
        {{ t('labels.date_added') }} <span :title="props.date">{{ props.date }}</span>
      </div>
      <div v-if="props.active" class="mt-2 flex items-center text-main">
        <div class="p-1 inline-block rounded-full" style="background-color: var(--base-primary-color)">
          <IconCheck :height="8" :width="8" fill="#fff" />
        </div>
        <span class="text-caption-medium mx-2">{{ t('pages.auth_login_limit.current_device') }}</span>
      </div>
    </div>
  </div>
</template>

